import React, { Component } from 'react';
import Navigation from './Navigation/Navigation'
import './App.css';
import SideMenu from './SideMenu/SideMenu'
import Home from './Home/Home'
import Decks from './Decks/Decks'
import Cards from './Cards/Cards'
import Stats from './Stats/Stats'
import About from './About/About'
import DeleteModal from './Modals/DeleteModal/DeleteModal'
import EditModal from './Modals/EditModal/EditModal';
import ViewModal from './Modals/ViewModal/ViewModal';
import AddModal from './Modals/AddModal/AddModal';

const helpers = {
  selectOption: function(i) {
      document.querySelectorAll('.menu-item').forEach((item, index) => {
          item.classList.remove('menu-item-clicked')
          if (index === i) {
              item.classList.add('menu-item-clicked')
          }
      })
  }
}

class App extends Component {
  constructor() {
    super()
    this.state = {
      menuOption: "home",
      displayModal: "",
      currentPage: "home"
    }
  }

  componentDidMount() {
    this.selectItem();
  }

  selectItem = () => {
    switch(this.state.menuOption) {
      case "home":
        helpers.selectOption(0)
        break;
      case "decks":
        helpers.selectOption(1)
        break;
      case "stats":
        helpers.selectOption(2)
        break;
      case "about":
        helpers.selectOption(3)
        break;
      default: 
        break;   
    }
  }
  
  setSelectedItem = (option) => {
    this.setState({
      menuOption: option,
      currentPage: option
    }, ()=>{this.selectItem()});    
  }

  onClickAdd = () => {
    const modal = (this.state.currentPage === "decks") ? "addDeck" : "addCard";
    this.setState({displayModal: modal})
  }

  onClickDelete = () => {
    const modal = (this.state.currentPage === "decks") ? "deleteDeck" : "deleteCard";
    this.setState({displayModal: modal})
  }

  onClickEdit = () => {
    const modal = (this.state.currentPage === "decks") ? "editDeck" : "editCard";
    this.setState({displayModal: modal})
  }

  onClickView = () => {
    (this.state.currentPage === "decks") ?
      this.setState({currentPage: "cards"}) :
      this.setState({displayModal: "viewCard"})
  }
  
  render(){
    const { menuOption, displayModal, currentPage} = this.state;

    const page = (menuOption === 'home') ? <Home /> :
      ((menuOption === 'decks') ? 
        ((currentPage === "decks") ? 
          <Decks 
            onClickAdd={this.onClickAdd} 
            onClickView={this.onClickView} 
            onClickEdit={this.onClickEdit} 
            onClickDelete={this.onClickDelete}/> :
          ((currentPage === "cards") ?
            <Cards 
              onClickAdd={this.onClickAdd} 
              onClickView={this.onClickView} 
              onClickEdit={this.onClickEdit} 
              onClickDelete={this.onClickDelete}/> : 
            null)) :
        ((menuOption === 'stats') ? <Stats /> :
          ((menuOption === 'about') ? <About /> :null)))

    const modal = (displayModal === "viewCard") ? <ViewModal /> :
      ((displayModal === "editCard") ? <EditModal /> :
        ((displayModal === "editDeck") ? <EditModal /> :
          ((displayModal === "deleteCard") ? <DeleteModal /> :
            ((displayModal === "deleteDeck") ? <DeleteModal /> :
              ((displayModal === "addCard") ? <AddModal /> :
                ((displayModal === "addDeck") ? <AddModal /> : null))))))

    return (
      <div className="df f-d-column">
        <Navigation />
        <div className="f-f-g-1 df f-a-i-stretch">
          <SideMenu setSelectedItem = {this.setSelectedItem} selectItem={this.selectItem}/>
          <div className="f-f-g-1 relative">
            {page}
            {modal}
          </div>
        </div>
      </div>
    );
  }
}

export default App;