import React from 'react';
import logo from './logo.png';

const Navigation = () => {
    return (
        <div className="df f-j-c-space-between" style={{userSelect: "none", backgroundColor:"#120136", color: '#40bad5'}}>
            <img className='ma2' src={logo} alt='logo' width="80px" height='62.88px'/>
            <h1 className='j-s-center ml4 mr1'>Leitner Seven</h1>
            <ul className='ma2 df f-a-i-center'>
                <li className="di mt1 ml4 mr1">About</li>
                <li className="di mt1 ml4 mr1">Stats</li>
                <li className="di mt1 ml4 mr1">Sign Out</li>
            </ul>
        </div>
    )
}

export default Navigation;