import React from 'react';
import Title from '../Title/Title';
import MenuCard from '../MenuCard/MenuCard';
import Add from '../Buttons/Add/Add'

const Cards = ({ onClickView, onClickEdit, onClickDelete, onClickAdd }) => {
    
    return (
        <div className="tc df f-d-column absolute overlay">
            <Title title='Cards'/>
            <div className='f-f-g-1 df f-d-row f-j-c-center f-w-wrap f-a-c-center pt4'>
                <MenuCard onClickView={onClickView} onClickEdit={onClickEdit} onClickDelete={onClickDelete} />
                <MenuCard onClickView={onClickView} onClickEdit={onClickEdit} onClickDelete={onClickDelete} />
                <MenuCard onClickView={onClickView} onClickEdit={onClickEdit} onClickDelete={onClickDelete} />
                <MenuCard onClickView={onClickView} onClickEdit={onClickEdit} onClickDelete={onClickDelete} />
                <MenuCard onClickView={onClickView} onClickEdit={onClickEdit} onClickDelete={onClickDelete} />
                <MenuCard onClickView={onClickView} onClickEdit={onClickEdit} onClickDelete={onClickDelete} />
            </div>
            <div className="mt4 mr5 df f-d-row f-j-c-end">
                <Add onClickAdd={onClickAdd} />
            </div>
        </div>
    )
}

export default Cards;